import React from 'react'

const FAQ = () => (
    <div style={{
        fontFamily: 'Raleway',
        fontSize: '18px',
        color: '#F9CF87',
        textShadow: '1px 1px black',
        height:'100%',
    }}>
    <h2>When will <b>Leaf</b> be done?</h2>
        <p>The team will continue to work on this project for as long as possible, but no estimate for final build.</p>

        <h2>What tools have you used?</h2>
        <p>We used Unity game engine and C# mostly. In level design we used Tiled map editor.</p>

        <h2>Source code</h2>
        <p>Under GitHub link in the footer or click <a href={'https://github.com/harrimahonen/Leaf-unity'}><b style={{color:'red'}}>here</b></a></p>
    </div>

)

export default FAQ



